<template>
  <div class="wrapper">
    <div class="content">
      <div class="w-left">
        <!--<ctitle :title="`${curCity==='广东省'?'全省':curCity}信访量`"></ctitle>-->
        <div class="charts">
          <div class="w-radio">
            <el-radio
              v-for="(item,index) of radioArr"
              :key="index"
              v-model="radio"
              :label="item"
              @change="handleRadio"
            >
              {{item}}
            </el-radio>
          </div>
          <div v-if="mapParams.options.JSON!=='440000'" class="map-back" @click="mapBack"></div>
          <transition name="fade">
            <Map v-if="mapFlag" :params="mapParams" @change="handleMap"></Map>
          </transition>
        </div>
      </div>
      <div class="w-right">
        <div class="part ct-top">
          <ctitle :title="`${shej_02}${shij_02==='invalid'?'':shij_02}${xj_02==='invalid'?'':xj_02}热点问题分布`"></ctitle>
          <div class="chart">
            <wordcloud :params="rdwtCloudParams" @canvasClick="reciClick"></wordcloud>
          </div>
        </div>
        <div class="part ct-bottom">
          <ctitle
            :title="`${shej_02}${shij_02==='invalid'?'':shij_02}${xj_02==='invalid'?'':xj_02}${curReci==='drop'?'':curReci}受理量走势`"></ctitle>
          <div class="chart">
            <LinesR :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import timeChange from "@/components/timeChange/index";
  import ctitle from "@/components/title/index";
  import wordcloud from "@/components/wordcloud/wordcloud";
  import LinesR from "@/components/line/LinesR";
  import Map from "@/components/map/Map";

  export default {
    name: "xstz",
    components: {
      ctitle,
      LinesR,
      wordcloud,
      timeChange,
      Map,
    },
    data() {
      return {
        radioArr: ['信访量', '信访事项'],
        radio: '信访量',
        curCity: "广东省",
        curReci: "drop",
        shijCity: "",
        shej_02: "invalid",
        shij_02: "invalid",
        xj_02: "invalid",
        nameP: "",
        mapJson: {
          "广东省": "440000",
          "广州市": "440100",
          "韶关市": "440200",
          "深圳市": "440300",
          "珠海市": "440400",
          "汕头市": "440500",
          "佛山市": "440600",
          "江门市": "440700",
          "湛江市": "440800",
          "茂名市": "440900",
          "肇庆市": "441200",
          "惠州市": "441300",
          "梅州市": "441400",
          "汕尾市": "441500",
          "河源市": "441600",
          "阳江市": "441700",
          "清远市": "441800",
          "东莞市": "441900",
          "中山市": "442000",
          "潮州市": "445100",
          "揭阳市": "445200",
          "云浮市": "445300",
        },
        //1.地图
        mapFlag: false,
        mapParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            isHome: false,
            JSON: "440000",
            xName: "name",
            yName: "value",
            map: {
              "value": "信访量"
            },
            carousel: true
          }
        },
        //2.热点问题分布
        rdwtCloudParams: {
          canvasStyle: {
            width: "100%",
            height: "100%",
          },
          data: [],
          options: {
            isClick: true
          }
        },
        // 3.受理量走势
        zsLineParams: {
          data: [],
          colors: [
            {
              line: "rgba(220, 150, 209, 1)",
              start: "rgba(220, 150, 209, 0.3)",
              end: "rgba(220, 150, 209, 0)"
            },
          ],
          option: {
            splitLineShow: true,
            legendShow: false,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "月",
            axisYName: "件次",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: false,
            areaShow: 1,
            smoothMonotone: "none",
            dataZoomshow: false,
            legendIcon: "rect",
            left: "8%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "2%",
            legendRight: "10%"
          },
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          map: {
            "value": "数量",
          },
          xName: "name"
        },
      };
    },
    mounted() {
      this.getMapAPI(this.date);
    },
    methods: {
      // 1.全省信访量-地图
      getMapAPI(date) {
        // 处理接口参数
        // 省级
        if (this.curCity === '广东省') {
          this.shej_02 = this.curCity;
          this.shij_02 = 'invalid';
          this.xj_02 = 'invalid';
          this.nameP = 'shij_02';
        } else {
          // 市级
          if (this.shijCity) {
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = 'invalid';
            this.nameP = 'xj_02';
          } else {
            // 县区级
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = this.curCity;
            this.nameP = 'invalid';
          }
        }
        get_http("zrdwhxQstzMap", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.mapParams.data = res.data.data;
            this.mapFlag = true;
            this.getAPI(date)
          })
          .catch(err => {
            console.log(err);
          });
      },
      getAPI(date) {
        this.getCloudAPI(date);
        this.getZsLineAPI(date);
      },
      // 2.热点问题分布
      getCloudAPI(date) {
        get_http("zrdwhxQstzRcCloud", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.rdwtCloudParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 3.受理量走势
      getZsLineAPI(date) {
        get_http("zrdwhxQstzzsLine", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          yjnr: this.curReci,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.zsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击地图下钻
      handleMap(name) {
        this.curCity = name;
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            this.mapFlag = false;
            this.mapParams.options.JSON = this.mapJson[name];
          }
        }
      },
      // 点击热词
      reciClick(name) {
        this.curReci = name;
        this.getZsLineAPI(this.date);
      },
      // 地图返回全省
      mapBack() {
        this.mapFlag = false;
        this.curCity = "广东省";
        this.mapParams.options.JSON = "440000";
      },
      // 点击单选框
      handleRadio(name){
        // 这里name后续需给接口传参
        get_http("zrdwhxQstzMap", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          day: `[${this.date.startDate},${this.date.endDate}]`
        })
          .then(res => {
            this.mapParams.options.map.value=name;
            this.mapParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      curCity: function (newVal, oldVal) {
        this.curReci = "drop";
        if (newVal === '广东省') {
          this.getMapAPI(this.date);
        } else {
          let c = '';
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.getMapAPI(this.date);
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            this.getAPI(this.date)
          }
        }
      },
      date(val) {
        this.getMapAPI(val);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper /deep/ .el-radio__label
    font-size .18rem

  .wrapper /deep/ .el-radio__input.is-checked + .el-radio__label
    color #f996d6

  .wrapper /deep/ .el-radio__input.is-checked .el-radio__inner
    border-color #f996d6
    background-color #f996d6

  .wrapper /deep/ .el-radio
    color #cbfbff
    display block
    line-height .35rem

  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .content
      width 100%
      height 100%
      display flex
      padding .2rem .3rem
      box-sizing border-box
      .chart
        box-sizing border-box
        width 100%
        height calc(100% - 0.28rem)
      .w-left
        position relative
        flex 1
        display flex
        flex-direction column
        .charts
          position relative
          flex 1
          width 100%
          height 100%
          .w-radio
            position absolute
            top .5rem
            left 1rem
            z-index 10
        .fade-enter-active, .fade-leave-active
          transform scale(1)
          transition all 0.8s

        .fade-enter, .fade-leave-to
          opacity 0
          transform scale(0)
        .map-back
          position absolute
          top 30px
          right 60px
          z-index 1000
          width 30px
          height 30px
          background url("~@/assets/back-icon.png") no-repeat
          cursor pointer
      .w-right
        display flex
        flex 1
        flex-direction column
        box-sizing border-box
        .part
          position relative
          flex 1
          width 100%
          height 100%
        .ct-top
          .chart
            width 80%
            margin 0 auto
            background url("~@/assets/part-bg-s.png") no-repeat center
            background-size 100% 100%
        .ct-bottom
          width 100%
</style>
